import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import persik from '../img/persik.png';
import './Persik.css';
import { TabbarItem } from '@vkontakte/vkui';
import Icon24NewsFeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24Home from '@vkontakte/icons/dist/24/home';
import Icon24Education from '@vkontakte/icons/dist/24/education';
import Tabbar from '@vkontakte/vkui/dist/components/Tabbar/Tabbar';

const osName = platform();

const Useful = ({props, id, go, fetchedUser }) => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			Persik
		</PanelHeader>
		<img className="Persik" src={persik} alt="Persik The Cat"/>
        <Tabbar>
			<TabbarItem text="Главная" onClick={go} data-to="home"><Icon24Home/>
			</TabbarItem>
			<TabbarItem text="Полезное" onClick={go} data-to="useful"><Icon24Education/>
			</TabbarItem>
			<TabbarItem text="Новости" onClick={go} data-to="news"><Icon24NewsFeed/>
			</TabbarItem>
		</Tabbar>
	</Panel>
);

Useful.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Useful;